.story {
  background-color: rgb(254, 172, 90);
  scroll-snap-align: start;
  padding-bottom: 50px;
}

.storyTextContainer p {
  color: white;
  font-size: 12px;
  padding-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
  text-align: center;
}

.storyImageContainer img {
  width: 50%;
  margin-left: 25%;
  margin-top: 60px;
  margin-bottom: 20px;
}

.story button {
  background-color: rgba(255, 255, 255, 0);
  width: 40;
  border: 0.15vw solid white;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  font-size: 12px;
  margin: 10px 35%auto;
  cursor: pointer;
}

.story button:hover {
  background-color: rgba(255, 255, 255, 1);
  color: rgb(254, 172, 90);
}

@media all and (min-width: 800px) {
  .story {
    display: flex;
    align-items: center;
    height: 100vh;
  }
  .storyTextContainer {
    margin-right: 10vw;
    width: 50%;
  }

  .storyImageContainer {
    width: 60%;
  }
}
