.product {
  background-color: rgb(254, 172, 90);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  scroll-snap-align: start;
  min-height: 100vh;
}

.product .twoRowLeft {
  display: flex;
  align-items: center;
  position: relative;
}
.product .twoRowRight {
  display: flex;
  align-items: center;
  position: relative;
}
.product .twoRowLeft .textField {
  text-align: center;
  margin-top: 100px;
}
.product .twoRowLeft .textField p {
  color: white;
  font-size: 18px;
  font-weight: 800;
}

.product .twoRowRight img {
  width: 100%;
}

@media all and (min-width: 600px) {
  .product {
    background-color: rgb(77, 198, 189);
    display: flex;
  }
  .product .twoRowLeft {
    width: 50%;
    display: flex;
    align-items: center;
    position: relative;
  }
  .product .twoRowRight {
    width: 50%;
    display: flex;
    align-items: center;
    position: relative;
  }
  .product .twoRowLeft .textField {
    width: 80%;
    margin-left: 20%;
  }
  .product .twoRowLeft .textField p {
    color: white;
    font-size: 2vw;
    font-weight: 800;
    margin-left: 0px;
    z-index: 10;
  }

  .product .twoRowRight img {
    width: 80%;
  }
  .product .yellowBar1 {
    background-color: rgb(254, 172, 90);
    width: 10vw;
    height: 1vh;
    position: absolute;
    top: 60.5vh;
    left: 18vw;
  }
  .product .yellowBar2 {
    background-color: rgb(0, 170, 212);
    width: 14vw;
    height: 1vh;
    position: absolute;
    top: 49vh;
    left: 21.5vw;
  }
  .product .yellowBar3 {
    background-color: rgb(255, 85, 85);
    width: 6vw;
    height: 1vh;
    position: absolute;
    top: 60.5vh;
    left: 31vw;
  }
}
