/* Introduction Section - Basic Styling ------------------------------------------*/

.titlePage {
  background: url(../BackgroundIntro.gif);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  display: flex;
  align-items: center;
  z-index: -1000;
}

.textField {
  margin: 10% 22%;
}
.titlePage {
  background: url(../BackgroundIntro.gif);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  display: flex;
  align-items: center;
  position: relative;
}

img.introLogo {
  position: absolute;
  left: 15%;
  top: 30%;
  width: 70%;
  z-index: 1000;
}

img.kikoSkateMonkeyTop {
  width: 15%;
  position: absolute;
  left: 40%;
  top: 30%;
  transform: rotate(-5deg);
  transition: all 2s;
}

img.kikoSkateMonkeyBottom {
  width: 15%;
  position: absolute;
  left: 100%;
  top: 80%;
  transform: rotate(-5deg);
  transition: all 2s;
}

@media all and (min-width: 600px) {
  .titlePage {
    background: url(../BackgroundIntro.gif);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    display: flex;
    align-items: center;
    z-index: -1000;
  }

  .textField {
    margin: 10% 22%;
  }
  .titlePage {
    background: url(../BackgroundIntro.gif);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 100vh;
    display: flex;
    align-items: center;
    position: relative;
  }

  img.introLogo {
    position: absolute;
    left: 30%;
    top: 22%;
    width: 40%;
    z-index: 1000;
  }

  img.kikoSkateMonkeyTop {
    width: 15%;
    position: absolute;
    left: 40%;
    top: 30%;
    transform: rotate(-5deg);
    transition: all 2s;
  }

  img.kikoSkateMonkeyBottom {
    width: 15%;
    position: absolute;
    left: 100%;
    top: 80%;
    transform: rotate(-5deg);
    transition: all 2s;
  }
}
