.green {
  background-color: rgb(77, 198, 189);
}
.yellow {
  background-color: rgb(254, 172, 90);
}
.blue {
  background-color: rgb(0, 170, 212);
}

.red {
  background-color: rgb(255, 85, 85);
}

.flavours {
  display: block;
  scroll-snap-align: start;
}

.threeColumn {
  display: flex;
  align-items: center;
  height: 100vh;
  scroll-snap-align: start;
}

.threeColumn div {
  margin-top: 30px;
}

.flavourImage {
  width: 90%;
  display: flex;
  justify-content: center;
  margin-top: 10px;
  margin-bottom: 10px;
}

.flavourImage img {
  width: 60%;
}

.flavourText {
  width: 80%;
  font-size: 14px;
  display: block;
  color: white;
  margin: 0% 10% 10% 10%;
  text-align: center;
}

button {
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid white;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  margin: 10% 2px;
  cursor: pointer;
}

@media all and (min-width: 600px) {
  .flavours {
    display: flex;
    height: 100vh;
    scroll-snap-align: start;
  }

  .threeColumn {
    width: 33.333%;
    height: 100%;
    display: flex;
    align-items: center;
  }

  .threeColumn div {
    margin-top: 8vh;
  }

  .flavourImage {
    width: 90%;
    display: flex;
    justify-content: center;
    margin-top: 3vh;
    margin-bottom: 3vh;
  }

  .flavourImage img {
    width: 60%;
  }

  .flavourText {
    width: 80%;
    font-size: 1vw;
    display: block;
    color: white;
    margin: 0% 10% 5% 10%;
    text-align: center;
  }

  button {
    background-color: rgba(255, 255, 255, 0);
    border: 0.15vw solid white;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 1vw;
    margin: 50px 2px;
    cursor: pointer;
  }

  button:hover {
    background-color: rgba(255, 255, 255, 1);
    color: black;
  }
}
